/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

h1, h2, h3, h4, h5, h6, p, div, span {
  color: #fefefe; }

h1 {
  font-size: 32px; }

h2 {
  font-size: 28px; }

h3 {
  font-size: 24px; }

h4 {
  font-size: 20px; }

h5 {
  font-size: 18px; }

h6 {
  font-size: 16px; }

.dfo-ds_button_root {
  font-size: 15px;
  padding: 10px 15px;
  background-color: #a8a8a8;
  color: #fefefe;
  font-weight: bold;
  text-decoration: none;
  margin-top: 25px;
  display: inline-block;
  border: 0;
  cursor: pointer; }
  .dfo-ds_button_root.dfo-ds_button_primary {
    background-color: #8653f6; }
  .dfo-ds_button_root.dfo-ds_button_secondary {
    background-color: #ff007a; }

.dfo-ds_card_root {
  width: 100%;
  box-sizing: border-box;
  padding: 20px;
  background-color: #333333;
  color: #fefefe;
  border-radius: 25px; }

.dfo-ds_container_root {
  max-width: 990px;
  box-sizing: border-box;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center; }

.dfo-ds_heading_root {
  color: #fefefe; }
  .dfo-ds_heading_root.dfo-ds_heading_primary {
    color: #8653f6; }
  .dfo-ds_heading_root.dfo-ds_heading_secondary {
    color: #ff007a; }
